import { useState } from "react";
import { motion } from "framer-motion";
import Confetti from "react-confetti";
import { Typewriter } from "react-simple-typewriter";
import { Card, CardContent, CardActions, Button } from "@mui/material";

const messages = [
  "Your smile lights up my world. ",
  "Your kindness melts my heart. ",
  "You are the most beautiful person, inside and out. ",
  "Life with you is a dream come true. ",
  "Will you be mine forever? "
];

const funnyResponses = [
  "Wait... Are you sure? ",
  "Think again, no pressure... but are you REALLY sure? ",
  "Oh no! My heart is breaking  Try one more time!",
  "Okay, now I'm crying ... Please reconsider!",
  "I'm not going anywhere! I'll wait... forever. "
];

const successMessages = [
  "Woohoo!  You made the best choice ever! ",
  "OMG! I'm the happiest person alive! ",
  "Let's celebrate with pizza and cuddles! ",
  "You just unlocked the 'Best Decision Ever' achievement! ",
  "Get ready for a lifetime of love, laughter, and weird inside jokes! "
];

export default function ProposalApp() {
  const [step, setStep] = useState(0);
  const [showConfetti, setShowConfetti] = useState(false);
  const [showFunny, setShowFunny] = useState(false);
  const [funnyIndex, setFunnyIndex] = useState(0);
  const [showSuccess, setShowSuccess] = useState(false);
  const [successIndex, setSuccessIndex] = useState(0);

  const handleNext = () => {
    if (step < messages.length - 1) {
      setStep(step + 1);
    }
  };

  const handleYes = () => {
    setShowConfetti(true);
    setShowSuccess(true);
    setSuccessIndex(Math.floor(Math.random() * successMessages.length));
  };

  const handleNo = () => {
    setShowFunny(true);
    setFunnyIndex((prevIndex) => (prevIndex + 1) % funnyResponses.length);
  };

  return (
    <div className="flex flex-col items-center justify-center h-screen bg-gradient-to-r from-pink-300 to-purple-400 text-white relative">
      {showConfetti && <Confetti />}
      <motion.div
        key={step}
        initial={{ opacity: 0, scale: 0.8 }}
        animate={{ opacity: 1, scale: 1 }}
        transition={{ duration: 0.6 }}
        className="w-96"
      >
        <Card sx={{ maxWidth: 400, padding: 2, textAlign: 'center', boxShadow: 6, borderRadius: 3 }}>
          <CardContent>
            <h2 style={{ fontSize: '1.5rem', fontWeight: 'bold' }}>
              <Typewriter words={[
                showSuccess ? successMessages[successIndex] : showFunny ? funnyResponses[funnyIndex] : messages[step]
              ]} loop={false} cursor cursorStyle="|" typeSpeed={50} />
            </h2>
          </CardContent>
          <CardActions style={{ justifyContent: 'center', gap: '1rem' }}>
            {!showSuccess && (step < messages.length - 1 ? (
              <Button variant="contained" color="secondary" onClick={handleNext} size="large">
                Next 
              </Button>
            ) : (
              <>
                <Button variant="contained" color="success" onClick={handleYes} size="large">
                  Yes! 
                </Button>
                <Button variant="contained" color="error" onClick={handleNo} size="large">
                  No 
                </Button>
              </>
            ))}
          </CardActions>
        </Card>
      </motion.div>
    </div>
  );
}

